import React from "react"
import { IconButton } from "@rp/react/web/mui/core"
import { ChatBubbleOutline } from "@rp/react/web/mui/icons"

type ChatPaneButtonProps = {
  chat: () => void
  disabled: boolean
}

function ChatPaneButton({ chat, disabled }: ChatPaneButtonProps) {
  return (
    <IconButton
      data-testid="chat-button"
      onClick={chat}
      sx={{
        height: "30px",
        width: "30px",
        "&:disabled": {
          cursor: "not-allowed",
          pointerEvents: "all",
        },
      }}
      disabled={disabled}
    >
      <ChatBubbleOutline
        sx={theme => ({
          color: disabled ? theme.palette.text.disabled : theme.palette.text.secondary,
          height: "20px",
          width: "20px",
        })}
      />
    </IconButton>
  )
}

export { ChatPaneButton }
